/* 
This document contains the general styling for the Making Matters book.
Styles can be overwritten from the CSS view in the wiki.

https://pagedjs.org/documentation/
https://jamonserrano.github.io/plumber-sass/
*/
/* Paged and print specific styles are collected in paged.css */
/* Fonts & typography & baseline grid 
the baseline grid is tightly coupled (by Plumber) to 
the typeface so you find them combined in this file.

It intentionally verbose in declaring the styles to
make it easier to adapt. 
*/
/* Fonts */
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-60.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-60.woff") format("woff");
  font-weight: 60;
}
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-90.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-90.woff") format("woff");
  font-weight: 90;
}
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-130.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-130.woff") format("woff");
  font-weight: 130;
}
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-150.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-150.woff") format("woff");
  font-weight: 150;
}
.font-authentic {
  font-family: Authentic, sans-serif;
  font-weight: 90;
}

/* Fonts */
@font-face {
  font-family: "notcourier";
  src: url("../fonts/NotCourierSans/notcouriersans_regular_macroman/NotCourierSans-webfont.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "notcourier";
  src: url("../fonts/NotCourierSans/notcouriersans_bold_macroman/NotCourierSans-Bold-webfont.woff") format("woff");
  font-weight: bold;
}
.font-notcourier {
  font-family: notcourier, sans-serif;
  font-weight: 90;
}

body {
  font-family: Authentic, sans-serif;
  font-weight: 90;
}